import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuDate from '@/components/custom/cu-date.vue'
import { api } from '@/config/common.js'
import { querySalesDetail, querySalesReturnDetail } from '@/api/data.js'
import { dateFormat, numberFormat } from '@/utils/tools.js'
export default {
	components: {
		uniList,
		uniListItem,
		cuPanel,
		cuCell,
		cuDate
	},
	data() {
		return {
			title: '销售分析',
			datas: [],
			totalOrder: 0,
			totalAmount: 0,
			totalProfit: 0,
			date: '',
			startDate: '',
			endDate: '',
			initFilter: [
				{ id: 0, name: '日期', checked: true, condition: false },
				{ id: 1, name: '客户', checked: false, condition: true }
			],
			filter: 0,
			cutomerName: ''
		}
	},
	onLoad(options) {
		this.date = options.date
		this.startDate = options.startDate
		this.endDate = options.endDate
		this.getData()
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		initDate() {
			return {
				title: '销售分析',
				date: this.date,
				startDate: this.startDate,
				endDate: this.endDate
			}
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		getData() {
			let reqData = {}
			if (this.filter == 0) {
				reqData = {
					startDate: this.startDate,
					endDate: this.endDate,
					groupBy: this.filter
				}
			} else {
				reqData = {
					startDate: this.startDate,
					endDate: this.endDate,
					groupBy: this.filter,
					cutomerName: this.cutomerName
				}
			}
			this.$refs.loading.open()
			querySalesDetail(api.report, reqData).then(res => {
				this.$refs.loading.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.datas = res.data.data
					this.cutomerName = ''
				}
			}).catch(error => {
				this.$refs.loading.close()
			})
		},
		numberFilter(number) {
			return numberFormat(number)
		},
		handleNavTo(val = '') {
			let url = '/pages/bill/sell-list/sell-list'
			let params = ''
			if (this.filter == 0) {
				params = 'startDate='+this.startDate+'&endDate='+this.endDate
			} else if (this.filter == 1){
				params = 'startDate='+this.startDate+'&endDate='+this.endDate+'&cutomerName='+val
			}
			uni.navigateTo({
				url: url+'?'+params
			})
		},
		handleDateOk(val) {
			this.date = val.date
			this.startDate = val.startDate
			this.endDate = val.endDate
			this.getData()
		},
		handleFilterOk(val) {
			this.initFilter = this.initFilter.map((item)=>{
				if (item.id == val) {
					item.checked = true
				} else {
					item.checked = false
				}
				return item
			})
			this.filter = val
			this.getData()
		},
		handleFilterCancel() {
			this.cutomerName = ''
		}
	}
}
